        try {
            await fetch("/api/join", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(formData)
            });
        } catch (err) {
            console.error("Failed to count join", err);
        }

        localStorage.setItem("has_joined_event", "true");
